export const getHouseByYear = (year: number): HouseSalePoint | undefined => {
  return houseSaleData.find(house => house.year === year);
};

export const getYearRange = (): { min: number; max: number } => {
  const years = houseSaleData.map(house => house.year);
  return {
    min: Math.min(...years),
    max: Math.max(...years)
  };
};
