"use client";

import { useState } from "react";
import { HouseSalePoint } from "@/app/time-machine/data";

interface PropertyRecord {
  year: number;
  price: number;
  imageUrl: string;
  description: string;
  location: string;
  bedrooms: number;
  bathrooms: number;
  sqft: number;
  listingType?: string;
  marketCondition?: string;
}

interface HouseDetailCardProps {
  house: HouseSalePoint | PropertyRecord | null;
}

export default function HouseDetailCard({ house }: HouseDetailCardProps) {
  const [imageError, setImageError] = useState(false);
  const [imageLoading, setImageLoading] = useState(true);

  if (!house) {
    return (
      <div className="max-w-4xl mx-auto bg-white rounded-2xl shadow-lg p-8 text-center">
        <div className="text-gray-500">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-200 rounded-full flex items-center justify-center">
            <span className="text-2xl">üè†</span>
          </div>
          <h3 className="text-xl font-semibold mb-2">No Data Available</h3>
          <p>Select a year on the timeline to view house details</p>
        </div>
      </div>
    );
  }

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(price);
  };

  const formatSqft = (sqft: number) => {
    return new Intl.NumberFormat('en-US').format(sqft);
  };

  const handleImageError = () => {
    setImageError(true);
    setImageLoading(false);
  };

  const handleImageLoad = () => {
    setImageLoading(false);
  };

  return (
    <div className="max-w-4xl mx-auto bg-white rounded-2xl shadow-lg overflow-hidden transition-all duration-500 hover:shadow-xl">
      {/* Image Section */}
      <div className="relative h-80 bg-gray-200">
        {imageLoading && (
          <div className="absolute inset-0 flex items-center justify-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-black"></div>
          </div>
        )}
        
        {imageError ? (
          <div className="absolute inset-0 flex items-center justify-center bg-gray-100">
            <div className="text-center text-gray-500">
              <div className="text-6xl mb-4">üè†</div>
              <p className="text-lg font-medium">Image not available</p>
            </div>
          </div>
        ) : (
          <img
            src={house.imageUrl}
            alt={`House sold in ${house.year}`}
            className={`w-full h-full object-cover transition-opacity duration-300 ${
              imageLoading ? 'opacity-0' : 'opacity-100'
            }`}
            onError={handleImageError}
            onLoad={handleImageLoad}
          />
        )}
        
        {/* Year Badge */}
        <div className="absolute top-4 left-4 bg-black text-white px-4 py-2 rounded-lg font-bold text-lg">
          {house.year}
        </div>
        
        {/* Price Badge */}
        <div className="absolute top-4 right-4 bg-white text-black px-4 py-2 rounded-lg font-bold text-lg shadow-lg">
          {formatPrice(house.price)}
        </div>
      </div>

      {/* Details Section */}
      <div className="p-8">
        <div className="grid md:grid-cols-2 gap-8">
          {/* Left Column - Main Details */}
          <div className="space-y-6">
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-2">
                Property Details
              </h2>
              <p className="text-gray-600 text-lg leading-relaxed">
                {house.description}
              </p>
            </div>
            
            <div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Location</h3>
              <p className="text-gray-600 flex items-center">
                <span className="mr-2">üìç</span>
                {house.location}
              </p>
            </div>
          </div>

          {/* Right Column - Specifications */}
          <div className="space-y-6">
            <h3 className="text-lg font-semibold text-gray-900">Specifications</h3>
            
            <div className="grid grid-cols-2 gap-4">
              <div className="bg-gray-50 p-4 rounded-lg text-center">
                <div className="text-2xl font-bold text-black">{house.bedrooms}</div>
                <div className="text-sm text-gray-600 font-medium">Bedrooms</div>
              </div>
              
              <div className="bg-gray-50 p-4 rounded-lg text-center">
                <div className="text-2xl font-bold text-black">{house.bathrooms}</div>
                <div className="text-sm text-gray-600 font-medium">Bathrooms</div>
              </div>
              
              <div className="bg-gray-50 p-4 rounded-lg text-center col-span-2">
                <div className="text-2xl font-bold text-black">{formatSqft(house.sqft)}</div>
                <div className="text-sm text-gray-600 font-medium">Square Feet</div>
              </div>
            </div>

            {/* Price per sqft */}
            <div className="bg-black text-white p-4 rounded-lg text-center">
              <div className="text-xl font-bold">
                {formatPrice(Math.round(house.price / house.sqft))}
              </div>
              <div className="text-sm opacity-80">Price per sq ft</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
