"use client";

import { useState } from "react";

interface PropertyRecord {
  year: number;
  price: number;
  imageUrl: string;
  description: string;
  location: string;
  bedrooms: number;
  bathrooms: number;
  sqft: number;
  listingType: string;
  marketCondition: string;
}

interface PropertyHistoryResponse {
  address: string;
  totalRecords: number;
  yearRange: {
    earliest: number;
    latest: number;
  };
  records: PropertyRecord[];
}

interface PropertySearchProps {
  onSearchSuccess: (data: PropertyHistoryResponse) => void;
  onSearchError: (error: string) => void;
}

export default function PropertySearch({ onSearchSuccess, onSearchError }: PropertySearchProps) {
  const [address, setAddress] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const validateAddress = (addr: string): boolean => {
    if (!addr.trim()) {
      setError("Please enter an address");
      return false;
    }
    
    if (addr.length < 10) {
      setError("Please enter a complete address including city and state");
      return false;
    }
    
    return true;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    
    if (!validateAddress(address)) {
      return;
    }
    
    setLoading(true);
    
    try {
      const response = await fetch(`/api/property-history?address=${encodeURIComponent(address)}`);
      const data = await response.json();
      
      if (!response.ok) {
        throw new Error(data.error || 'Failed to fetch property data');
      }
      
      onSearchSuccess(data);
      setError("");
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'An unexpected error occurred';
      setError(errorMessage);
      onSearchError(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  const handleClear = () => {
    setAddress("");
    setError("");
  };

  const sampleAddresses = [
    "123 Main St, Red Bank, NJ",
    "456 Ocean Ave, Asbury Park, NJ",
    "789 Broad St, Freehold, NJ",
    "321 Shore Dr, Long Branch, NJ",
    "654 Park Ave, Marlboro, NJ"
  ];

  return (
    <div className="bg-white rounded-2xl shadow-lg p-8">
      <div className="space-y-6">
        {/* Header */}
        <div className="text-center space-y-2">
          <h2 className="text-2xl font-bold text-gray-900">Property Search</h2>
          <p className="text-gray-600">
            Enter a Monmouth County, NJ address to explore its complete sale history
          </p>
        </div>

        {/* Search Form */}
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="address" className="block text-sm font-medium text-gray-700 mb-2">
              Property Address
            </label>
            <div className="relative">
              <input
                id="address"
                type="text"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
                className="w-full p-4 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent text-lg"
                placeholder="e.g., 123 Main Street, Red Bank, NJ"
                disabled={loading}
              />
              {address && (
                <button
                  type="button"
                  onClick={handleClear}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600"
                  disabled={loading}
                >
                  ‚úï
                </button>
              )}
            </div>
          </div>

          <div className="flex gap-3">
            <button
              type="submit"
              disabled={loading || !address.trim()}
              className="flex-1 bg-black text-white py-3 px-6 rounded-lg font-semibold hover:bg-gray-800 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors duration-200"
            >
              {loading ? (
                <span className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                  Searching...
                </span>
              ) : (
                "üîç Search Property History"
              )}
            </button>
          </div>
        </form>

        {/* Error Display */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4">
            <div className="flex items-start">
              <div className="text-red-600 mr-3 mt-0.5">‚ö†Ô∏è</div>
              <div>
                <h4 className="text-red-800 font-medium">Search Error</h4>
                <p className="text-red-700 text-sm mt-1">{error}</p>
              </div>
            </div>
          </div>
        )}

        {/* Sample Addresses */}
        {!loading && (
          <div className="space-y-3">
            <h3 className="text-sm font-medium text-gray-700">Try these sample addresses:</h3>
            <div className="flex flex-wrap gap-2">
              {sampleAddresses.map((sampleAddr, index) => (
                <button
                  key={index}
                  onClick={() => setAddress(sampleAddr)}
                  className="text-sm bg-gray-100 hover:bg-gray-200 text-gray-700 px-3 py-2 rounded-lg transition-colors duration-200"
                >
                  {sampleAddr}
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Info Box */}
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
          <div className="flex items-start">
            <div className="text-blue-600 mr-3 mt-0.5">‚ÑπÔ∏è</div>
            <div className="text-sm">
              <h4 className="text-blue-800 font-medium">How it works</h4>
              <ul className="text-blue-700 mt-1 space-y-1">
                <li>‚Ä¢ Enter a complete address in Monmouth County, NJ</li>
                <li>‚Ä¢ View historical sale records from the 1960s to today</li>
                <li>‚Ä¢ Explore price trends and market conditions over time</li>
                <li>‚Ä¢ Get AI-powered analysis of market trends</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
