"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { houseSaleData, getHouseByYear, getYearRange, HouseSalePoint } from "./data";
import TimelineSlider from "@/components/TimeMachine/TimelineSlider";
import HouseDetailCard from "@/components/TimeMachine/HouseDetailCard";
import AIAnalysis from "@/components/TimeMachine/AIAnalysis";
import PropertySearch from "@/components/TimeMachine/PropertySearch";

interface PropertyRecord {
  year: number;
  price: number;
  imageUrl: string;
  description: string;
  location: string;
  bedrooms: number;
  bathrooms: number;
  sqft: number;
  listingType: string;
  marketCondition: string;
}

interface PropertyHistoryResponse {
  address: string;
  totalRecords: number;
  yearRange: {
    earliest: number;
    latest: number;
  };
  records: PropertyRecord[];
}

export default function TimeMachinePage() {
  const [currentYear, setCurrentYear] = useState(2000);
  const [currentHouse, setCurrentHouse] = useState<HouseSalePoint | PropertyRecord | null>(null);
  const [showAIAnalysis, setShowAIAnalysis] = useState(false);
  const [propertyHistory, setPropertyHistory] = useState<PropertyRecord[] | null>(null);
  const [searchAddress, setSearchAddress] = useState<string>("");
  const [searchError, setSearchError] = useState<string>("");
  const [isUsingRealData, setIsUsingRealData] = useState(false);

  // Default sample data
  const yearRange = getYearRange();
  const sampleAvailableYears = houseSaleData.map(house => house.year).sort((a, b) => a - b);
  
  // Current available years (either from search or sample data)
  const availableYears = propertyHistory 
    ? propertyHistory.map(record => record.year).sort((a, b) => a - b)
    : sampleAvailableYears;

  // Current year range (either from search or sample data)
  const currentYearRange = propertyHistory 
    ? { min: Math.min(...availableYears), max: Math.max(...availableYears) }
    : yearRange;

  // Update current house when year changes
  useEffect(() => {
    if (propertyHistory) {
      // Using real search data
      const record = propertyHistory.find(r => r.year === currentYear);
      setCurrentHouse(record || null);
    } else {
      // Using sample data
      const house = getHouseByYear(currentYear);
      setCurrentHouse(house || null);
    }
  }, [currentYear, propertyHistory]);

  // Initialize with first available year
  useEffect(() => {
    if (availableYears.length > 0) {
      setCurrentYear(availableYears[0]);
    }
  }, [availableYears]);

  const handleYearChange = (year: number) => {
    console.log(`Year changed to: ${year}`);
    setCurrentYear(year);
  };

  const handleSearchSuccess = (data: PropertyHistoryResponse) => {
    setPropertyHistory(data.records);
    setSearchAddress(data.address);
    setSearchError("");
    setIsUsingRealData(true);
    
    // Set to the most recent year
    if (data.records.length > 0) {
      const latestYear = Math.max(...data.records.map(r => r.year));
      setCurrentYear(latestYear);
    }
  };

  const handleSearchError = (error: string) => {
    setSearchError(error);
    setPropertyHistory(null);
    setSearchAddress("");
    setIsUsingRealData(false);
  };

  const handleBackToSample = () => {
    setPropertyHistory(null);
    setSearchAddress("");
    setSearchError("");
    setIsUsingRealData(false);
    setCurrentYear(sampleAvailableYears[0]);
  };

  const getMarketContext = () => {
    if (!currentHouse) return null;

    const year = currentHouse.year;
    let context = "";
    let contextColor = "text-gray-600";

    // Enhanced market context for extended timeline
    if (year >= 1960 && year <= 1970) {
      context = "Post-War Suburban Growth - Rapid development and homeownership expansion";
      contextColor = "text-green-600";
    } else if (year >= 1971 && year <= 1980) {
      context = "Economic Turbulence - High inflation and interest rates affecting housing";
      contextColor = "text-orange-600";
    } else if (year >= 1981 && year <= 1990) {
      context = "Reagan Era Recovery - Economic growth and housing market stabilization";
      contextColor = "text-blue-600";
    } else if (year >= 1991 && year <= 2000) {
      context = "Tech Boom Era - Economic expansion and rising property values";
      contextColor = "text-purple-600";
    } else if (year >= 2001 && year <= 2006) {
      context = "Housing Boom Era - Rapid price appreciation and easy credit";
      contextColor = "text-green-600";
    } else if (year >= 2007 && year <= 2009) {
      context = "Financial Crisis - Market peak and beginning of downturn";
      contextColor = "text-red-600";
    } else if (year >= 2010 && year <= 2012) {
      context = "Post-Crisis Recovery - Market bottom and stabilization";
      contextColor = "text-orange-600";
    } else if (year >= 2013 && year <= 2019) {
      context = "Steady Recovery - Gradual price increases and market normalization";
      contextColor = "text-blue-600";
    } else if (year >= 2020) {
      context = "Pandemic Era - Remote work driving demand and price surges";
      contextColor = "text-purple-600";
    }

    // Use market condition from real data if available
    if (isUsingRealData && 'marketCondition' in currentHouse) {
      context = currentHouse.marketCondition;
    }

    return { context, contextColor };
  };

  const marketContext = getMarketContext();

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <Link 
                href="/"
                className="text-gray-600 hover:text-gray-900 transition-colors duration-200"
              >
                ‚Üê Back to Home
              </Link>
              <div className="h-6 w-px bg-gray-300"></div>
              <h1 className="text-2xl font-bold text-gray-900">House Time Machine</h1>
              {isUsingRealData && (
                <div className="bg-green-100 text-green-800 px-3 py-1 rounded-full text-sm font-medium">
                  Real Data: {searchAddress}
                </div>
              )}
            </div>
            
            <div className="flex items-center space-x-3">
              {isUsingRealData && (
                <button
                  onClick={handleBackToSample}
                  className="bg-gray-100 text-gray-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-200 transition-colors duration-200"
                >
                  Back to Sample
                </button>
              )}
              <button
                onClick={() => setShowAIAnalysis(!showAIAnalysis)}
                className="bg-black text-white px-4 py-2 rounded-lg font-medium hover:bg-gray-800 transition-colors duration-200"
              >
                {showAIAnalysis ? "Hide AI Analysis" : "Show AI Analysis"}
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="space-y-8">
          {/* Property Search - Show only if not using real data */}
          {!isUsingRealData && (
            <PropertySearch 
              onSearchSuccess={handleSearchSuccess}
              onSearchError={handleSearchError}
            />
          )}

          {/* Search Results Summary */}
          {isUsingRealData && propertyHistory && (
            <div className="bg-white rounded-lg p-4 shadow-sm border-l-4 border-green-500">
              <div className="flex items-center justify-between">
                <div className="flex items-center">
                  <div className="mr-3 text-2xl">üè†</div>
                  <div>
                    <h3 className="font-semibold text-gray-900">Property Found</h3>
                    <p className="text-sm text-gray-600">
                      {propertyHistory.length} historical records from {Math.min(...availableYears)} to {Math.max(...availableYears)}
                    </p>
                  </div>
                </div>
                <button
                  onClick={handleBackToSample}
                  className="text-sm text-gray-500 hover:text-gray-700"
                >
                  Search Different Property
                </button>
              </div>
            </div>
          )}

          {/* Market Context Banner */}
          {marketContext && (
            <div className="bg-white rounded-lg p-4 shadow-sm border-l-4 border-black">
              <div className="flex items-center">
                <div className="mr-3 text-2xl">üìä</div>
                <div>
                  <h3 className="font-semibold text-gray-900">Market Context</h3>
                  <p className={`text-sm ${marketContext.contextColor}`}>
                    {marketContext.context}
                  </p>
                </div>
              </div>
            </div>
          )}

          {/* Timeline Slider */}
          <div className="bg-white rounded-2xl shadow-lg p-8">
            <TimelineSlider
              min={currentYearRange.min}
              max={currentYearRange.max}
              value={currentYear}
              onChange={handleYearChange}
              availableYears={availableYears}
            />
          </div>

          {/* House Detail Card */}
          <HouseDetailCard house={currentHouse} />

          {/* Statistics Overview */}
          {currentHouse && (
            <div className="bg-white rounded-2xl shadow-lg p-8">
              <h2 className="text-xl font-bold text-gray-900 mb-6">Market Statistics</h2>
              <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {availableYears.indexOf(currentYear) + 1}
                  </div>
                  <div className="text-sm text-gray-600">of {availableYears.length} records</div>
                </div>
                
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {currentYear < 1980 ? "üìà" : currentYear < 2010 ? "üìä" : currentYear < 2015 ? "üìâ" : "üìà"}
                  </div>
                  <div className="text-sm text-gray-600">Market Trend</div>
                </div>
                
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {Math.round(currentHouse.price / 1000)}K
                  </div>
                  <div className="text-sm text-gray-600">Price (thousands)</div>
                </div>
                
                <div className="text-center">
                  <div className="text-2xl font-bold text-black">
                    {Math.round(currentHouse.price / currentHouse.sqft)}
                  </div>
                  <div className="text-sm text-gray-600">$ per sq ft</div>
                </div>
              </div>
            </div>
          )}

          {/* AI Analysis Section */}
          {showAIAnalysis && (
            <AIAnalysis currentHouseData={currentHouse} />
          )}

          {/* Data Source Notice */}
          <div className="bg-gray-100 rounded-lg p-6 text-center">
            <p className="text-sm text-gray-600">
              <strong>Note:</strong> {isUsingRealData 
                ? "Using simulated historical data for demonstration. In production, this would connect to real MLS and property databases."
                : "This is a demonstration using sample data. Real estate prices and trends are simplified for educational purposes."
              }
            </p>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-white border-t mt-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center text-gray-600">
            <p className="text-sm">
              House Time Machine - Explore real estate history through interactive data visualization
            </p>
          </div>
        </div>
      </footer>
    </div>
  );
}
