"use client";

import { useState } from "react";
import { fetchAIAnalysis, DEFAULT_SYSTEM_PROMPT } from "@/lib/ai";

interface AIAnalysisProps {
  currentHouseData?: any;
}

export default function AIAnalysis({ currentHouseData }: AIAnalysisProps) {
  const [query, setQuery] = useState("");
  const [systemPrompt, setSystemPrompt] = useState(DEFAULT_SYSTEM_PROMPT);
  const [response, setResponse] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [showSystemPrompt, setShowSystemPrompt] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!query.trim()) {
      setError("Please enter a question about the real estate data.");
      return;
    }

    if (!systemPrompt.trim()) {
      setError("System prompt cannot be empty.");
      return;
    }

    setLoading(true);
    setError("");
    setResponse("");

    try {
      // Add context about current house data if available
      let contextualQuery = query;
      if (currentHouseData) {
        contextualQuery = `Context: Currently viewing a house sold in ${currentHouseData.year} for ${new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(currentHouseData.price)} at ${currentHouseData.location}. The house has ${currentHouseData.bedrooms} bedrooms, ${currentHouseData.bathrooms} bathrooms, and ${currentHouseData.sqft} square feet.

Question: ${query}`;
      }

      const aiResponse = await fetchAIAnalysis(contextualQuery, systemPrompt);
      setResponse(aiResponse);
    } catch (err) {
      console.error("AI Analysis error:", err);
      setError(err instanceof Error ? err.message : "An unexpected error occurred");
    } finally {
      setLoading(false);
    }
  };

  const clearAll = () => {
    setQuery("");
    setResponse("");
    setError("");
  };

  const sampleQuestions = [
    "Why did house prices drop around 2010-2012?",
    "What factors contributed to the price increase after 2015?",
    "How do current prices compare to historical trends?",
    "What economic events influenced these price changes?",
    "Is this a good time to buy based on historical data?"
  ];

  return (
    <div className="max-w-4xl mx-auto bg-white rounded-2xl shadow-lg p-8">
      <div className="space-y-6">
        {/* Header */}
        <div className="text-center space-y-2">
          <h2 className="text-2xl font-bold text-gray-900">AI Market Analysis</h2>
          <p className="text-gray-600">
            Get insights about real estate trends and market conditions
          </p>
        </div>

        {/* System Prompt Section */}
        <div className="space-y-3">
          <div className="flex items-center justify-between">
            <label className="text-sm font-medium text-gray-700">
              System Prompt (Customizable)
            </label>
            <button
              type="button"
              onClick={() => setShowSystemPrompt(!showSystemPrompt)}
              className="text-sm text-black hover:text-gray-700 font-medium"
            >
              {showSystemPrompt ? "Hide" : "Show & Edit"}
            </button>
          </div>
          
          {showSystemPrompt && (
            <textarea
              value={systemPrompt}
              onChange={(e) => setSystemPrompt(e.target.value)}
              className="w-full p-4 border border-gray-300 rounded-lg resize-none focus:ring-2 focus:ring-black focus:border-transparent"
              rows={8}
              placeholder="Enter system prompt to customize AI behavior..."
            />
          )}
        </div>

        {/* Query Form */}
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="query" className="block text-sm font-medium text-gray-700 mb-2">
              Your Question
            </label>
            <textarea
              id="query"
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              className="w-full p-4 border border-gray-300 rounded-lg resize-none focus:ring-2 focus:ring-black focus:border-transparent"
              rows={3}
              placeholder="Ask about real estate trends, market conditions, or specific insights..."
              disabled={loading}
            />
          </div>

          <div className="flex gap-3">
            <button
              type="submit"
              disabled={loading || !query.trim()}
              className="flex-1 bg-black text-white py-3 px-6 rounded-lg font-semibold hover:bg-gray-800 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors duration-200"
            >
              {loading ? (
                <span className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                  Analyzing...
                </span>
              ) : (
                "Get AI Analysis"
              )}
            </button>
            
            <button
              type="button"
              onClick={clearAll}
              className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors duration-200"
            >
              Clear
            </button>
          </div>
        </form>

        {/* Sample Questions */}
        {!response && !loading && (
          <div className="space-y-3">
            <h3 className="text-sm font-medium text-gray-700">Sample Questions:</h3>
            <div className="flex flex-wrap gap-2">
              {sampleQuestions.map((question, index) => (
                <button
                  key={index}
                  onClick={() => setQuery(question)}
                  className="text-sm bg-gray-100 hover:bg-gray-200 text-gray-700 px-3 py-2 rounded-lg transition-colors duration-200"
                >
                  {question}
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Error Display */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4">
            <div className="flex items-start">
              <div className="text-red-600 mr-3 mt-0.5">‚ö†Ô∏è</div>
              <div>
                <h4 className="text-red-800 font-medium">Error</h4>
                <p className="text-red-700 text-sm mt-1">{error}</p>
              </div>
            </div>
          </div>
        )}

        {/* Response Display */}
        {response && (
          <div className="bg-gray-50 border border-gray-200 rounded-lg p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-3">AI Analysis</h3>
            <div className="prose prose-gray max-w-none">
              <div className="whitespace-pre-wrap text-gray-700 leading-relaxed">
                {response}
              </div>
            </div>
          </div>
        )}

        {/* API Key Notice */}
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
          <div className="flex items-start">
            <div className="text-blue-600 mr-3 mt-0.5">‚ÑπÔ∏è</div>
            <div className="text-sm">
              <h4 className="text-blue-800 font-medium">API Configuration</h4>
              <p className="text-blue-700 mt-1">
                To use AI analysis, add your OpenRouter API key as <code className="bg-blue-100 px-1 rounded">NEXT_PUBLIC_OPENROUTER_API_KEY</code> in your environment variables.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
